<template>
  <div id="simple-calendar-app">
    <div class="vx-card no-scroll-content">
      <calendar-view
        ref="calendar"
        :displayPeriodUom="calendarView"
        :events="events"
        :show-date="showDate"
        enableDragDrop
        eventBorderHeight="0px"
        eventContentHeight="1.65rem"
        class="theme-default"
        @click-date="openAddNewEvent"
        @click-event="GetEvent"
        @drop-on-date="eventDragged"
      >
        <div slot="header" class="mb-4">
          <div class="vx-row no-gutter">
            <!-- Month Name -->
            <div class="vx-col w-1/3 items-center sm:flex hidden">
              <!-- Add new event button -->
              <vs-button
                icon-pack="feather"
                icon="icon-plus"
                @click="promptAddNewEvent(new Date())"
                >Ajouter</vs-button
              >
            </div>

            <!-- Current Month -->
            <div
              class="vx-col sm:w-1/3 w-full sm:my-0 my-3 flex sm:justify-end justify-center order-last"
            >
              <div class="flex items-center">
                <feather-icon
                  :icon="$vs.rtl ? 'ChevronRightIcon' : 'ChevronLeftIcon'"
                  @click="updateMonth(-1)"
                  svgClasses="w-5 h-5 m-1"
                  class="cursor-pointer bg-primary text-white rounded-full"
                />

                <span class="mx-3 text-xl font-medium whitespace-no-wrap">{{
                  showDate | month
                }}</span>

                <feather-icon
                  :icon="$vs.rtl ? 'ChevronLeftIcon' : 'ChevronRightIcon'"
                  @click="updateMonth(1)"
                  svgClasses="w-5 h-5 m-1"
                  class="cursor-pointer bg-primary text-white rounded-full"
                />
              </div>
            </div>

            <div class="vx-col sm:w-1/3 w-full flex justify-center">
              <template v-for="(view, index) in calendarViewTypes">
                <vs-button
                  v-if="calendarView === view.val"
                  :key="String(view.val) + 'filled'"
                  type="filled"
                  class="p-3 md:px-8 md:py-3"
                  :class="{
                    'border-l-0 rounded-l-none': index,
                    'rounded-r-none': calendarViewTypes.length !== index + 1,
                  }"
                  @click="calendarView = view.val"
                  >{{ view.label }}</vs-button
                >
                <vs-button
                  v-else
                  :key="String(view.val) + 'border'"
                  type="border"
                  class="p-3 md:px-8 md:py-3"
                  :class="{
                    'border-l-0 rounded-l-none': index,
                    'rounded-r-none': calendarViewTypes.length !== index + 1,
                  }"
                  @click="calendarView = view.val"
                  >{{ view.label }}</vs-button
                >
              </template>
            </div>
          </div>

          <div class="vx-row sm:flex hidden mt-4">
            <div class="vx-col w-full flex">
              <!-- Labels -->
              <div class="flex flex-wrap sm:justify-start justify-center">
                <div
                  v-for="(label, index) in labels"
                  :key="index"
                  class="flex items-center mr-4 mb-2"
                >
                  <div
                    class="h-3 w-3 inline-block rounded-full mr-2"
                    :class="'bg-' + label.color"
                  ></div>
                  <span>{{ label.text }}</span>
                </div>
                <div class="flex items-center mr-4 mb-2">
                  <div class="h-3 w-3 inline-block rounded-full mr-2 bg-primary"></div>
                  <span>Rien</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </calendar-view>
    </div>

    <!-- ADD EVENT -->
    <vs-prompt
      class="calendar-event-dialog"
      title="Ajouter d'évènement"
      accept-text="Ajouter"
      cancel-text="Annuler"
      @accept="AddEvent"
      :active.sync="activePromptAddEvent"
    >
      <small class="date-label">Label</small>
      <div class="calendar__label-container flex">
        <vs-chip
          v-if="labelLocal != 'none'"
          class="text-white"
          :class="'bg-' + labelColor(labelLocal)"

          >{{ labelLocal }}  </vs-chip
        >
        <div hidden v-if='(labelLocal == "Professionnel")'> {{event.label = "Professionnel"}} {{event.classes = "event-success"}}</div>
        <div hidden v-if='(labelLocal == "Mariage")'> {{event.label = "Mariage"}} {{event.classes = "event-warning"}}</div>
        <div hidden v-if='(labelLocal == "Fiançailles")'> {{event.label = "Fiançailles"}} {{event.classes = "event-danger"}}</div>
        <div hidden v-if='(labelLocal == "Circoncision")'> {{event.label = "Circoncision"}} {{event.classes = "event-primary"}}</div>
        <div hidden v-if='(labelLocal == "Contrat")'> {{event.label = "Contrat"}} {{event.classes = "event-dark"}}</div>

        <vs-dropdown
          vs-custom-content
          vs-trigger-click
          class="ml-auto my-2 cursor-pointer"
        >
          <feather-icon
            icon="TagIcon"
            svgClasses="h-5 w-5"
            class="cursor-pointer"
            @click.prevent
          ></feather-icon>

          <vs-dropdown-menu style="z-index: 200001">
            <vs-dropdown-item
              v-for="(label, index) in labels"
              :key="index"
              @click="labelLocal = label.value"

            >
              <div
                class="h-3 w-3 inline-block rounded-full mr-2"
                :class="'bg-' + label.color"
              ></div>
              <span>{{ label.text }}</span>
            </vs-dropdown-item>

            <vs-dropdown-item @click="labelLocal = 'none'">
              <div class="h-3 w-3 mr-1 inline-block rounded-full mr-2 bg-primary"></div>
              <span>Rien</span>
            </vs-dropdown-item>
          </vs-dropdown-menu>
        </vs-dropdown>
      </div>
      <div class="my-4">
        <small class="date-label">Date début</small>
        <datepicker name="start-date" v-model="startDate" :disabled="disabledFrom"></datepicker>
      </div>
      <div class="my-4 mb-4">
        <small class="date-label">Date fin</small>
        <datepicker :language="$vs.rtl ? langHe : langEn" :disabledDates="disabledDatesTo" name="end-date" v-model="endDate">
        </datepicker>
      </div>
      <vs-input
        name="event-name"
        v-validate="'required'"
        class="w-full  mt-5 mb-4"
        label-placeholder="Titre"
        v-model="event.title"
      ></vs-input>
      <vs-input
        name="event-name"
        v-validate="'required'"
        class="w-full mt-5 mb-3"
        label-placeholder="Nom client"
        v-model="event.client"
      ></vs-input>
      <vs-input
        name="event-name"
        v-validate="'required'"
        class="w-full mt-5 mb-4"
        label-placeholder="Local"
        v-model="event.local"
      ></vs-input>
      <vs-input
        name="event-url"
        class="w-full mt-6"
        label-placeholder="Commentaire (optionnel)"
        v-model="event.comment"
      ></vs-input>
    </vs-prompt>

    <!-- EDIT EVENT -->
    <vs-prompt
      class="calendar-event-dialog"
      title="Modification d'événement"
      @accept="PutEvent"
      @cancel="DeleteEvent"
      accept-text="Modifier"
      cancel-text="Supprimer"
      button-cancel="border"
      :active.sync="activePromptEditEvent"
    >
      <small class="date-label">Label</small>
      <div class="calendar__label-container flex">
        <vs-chip v-if="labelLocal != 'none'" class="text-white" :class="'bg-' + labelColor(labelLocal)">{{ labelLocal }}</vs-chip>

        <div hidden v-if='(labelLocal == "Professionnel")'> {{eventData.label = "Professionnel"}} {{eventData.classes = "event-success"}}</div>
        <div hidden v-if='(labelLocal == "Mariage")'> {{eventData.label = "Mariage"}} {{eventData.classes = "event-warning"}}</div>
        <div hidden v-if='(labelLocal == "Fiançailles")'> {{eventData.label = "Fiançailles"}} {{eventData.classes = "event-danger"}}</div>
        <div hidden v-if='(labelLocal == "Circoncision")'> {{eventData.label = "Circoncision"}} {{eventData.classes = "event-primary"}}</div>

        <vs-dropdown vs-custom-content vs-trigger-click class="ml-auto my-2 cursor-pointer">
          <feather-icon icon="TagIcon" svgClasses="h-5 w-5" class="cursor-pointer" @click.prevent></feather-icon>

          <vs-dropdown-menu style="z-index: 200001">
            <vs-dropdown-item v-for="(label, index) in labels" :key="index" @click="labelLocal = label.value">
              <div class="h-3 w-3 inline-block rounded-full mr-2" :class="'bg-' + label.color"></div>
              <span>{{ label.text }}</span>
            </vs-dropdown-item>

            <vs-dropdown-item @click="labelLocal = 'none'">
              <div class="h-3 w-3 mr-1 inline-block rounded-full mr-2 bg-primary"></div>
              <span>Rien</span>
            </vs-dropdown-item>
          </vs-dropdown-menu>
        </vs-dropdown>
      </div>
      <div class="my-4">
        <small class="date-label">Date début</small>
        <datepicker name="start-date" v-model="eventData.startDate" :disabled="disabledFrom"></datepicker>
      </div>
      <div class="my-4">
        <small class="date-label">Date fin</small>
        <datepicker :language="$vs.rtl ? langHe : langEn" :disabledDates="disabledDatesTo2" name="end-date" v-model="eventData.endDate">
        </datepicker>
      </div>
      <vs-input name="event-name" v-validate="'required'" class="w-full" label-placeholder="Titre" v-model="eventData.title"
       ></vs-input>
      <vs-input name="event-name" v-validate="'required'" class="w-full" label-placeholder="Nom client"
        v-model="eventData.client"></vs-input>
      <vs-input name="event-name" v-validate="'required'" class="w-full" label-placeholder="Local" v-model="eventData.local">
      </vs-input>
      <vs-input name="event-url" class="w-full mt-6" label-placeholder="Commentaire" v-model="eventData.comment"></vs-input>
    </vs-prompt>
  </div>
</template>

<script>
import { CalendarView, CalendarViewHeader } from "vue-simple-calendar";
import moduleCalendar from "@/store/calendar/moduleCalendar.js";
require("vue-simple-calendar/static/css/default.css");

import Datepicker from "vuejs-datepicker";
import { en, he } from "vuejs-datepicker/src/locale";
import moment from "moment";
import axios from 'axios'

export default {
  components: {
    CalendarView,
    CalendarViewHeader,
    Datepicker,
  },
  data() {
    return {
      eventData: [],
      startDate: '',
      endDate: '',
      event: {
        title: '',
        client: '',
        local: '',
        startDate: '',
        endDate: '',
        classes: '',
        label: '',
        comment: ''

      },
      events: [
      ],
      labels: [
        { text: "Professionnel", value: "Professionnel", color: "success" },
        { text: "Mariage", value: "Mariage", color: "warning" },
        { text: "Fiançailles", value: "Fiançailles", color: "danger" },
        { text: "Circoncision", value: "Circoncision", color: "primary" },
        { text: "Contrat", value: "Contrat", color: "dark" },
      ],
      showDate: moment(this.showDate1).format("MM/DD/YYYY"),
      showDate1: new Date(),
      showDate2: new Date(),
      disabledFrom: false,
      id: 0,
      title: "",
      startDate: "",
      endDate: "",
      labelLocal: "none",

      langHe: he,
      langEn: en,

      url: "",
      calendarView: "month",

      activePromptAddEvent: false,
      activePromptEditEvent: false,

      calendarViewTypes: [
        {
          label: "Mois",
          val: "month",
        },
        {
          label: "Semaine",
          val: "week",
        },
      ],
    };
  },
  computed: {

    disabledDatesTo() {
      return { to: new Date(this.startDate) };
    },
    disabledDatesFrom() {
      return { from: new Date(this.endDate) };
    },
    disabledDatesTo2() {
      return { to: new Date(this.eventData.startDate) };
    },
    disabledDatesFrom2() {
      return { from: new Date(this.eventData.endDate) };
    },
    labelColor() {
      return (label) => {
        if (label == "Professionnel") return "success";
        else if (label == "Mariage") return "warning";
        if (label == "Fiançailles") return "danger";
        if (label == "Circoncision") return "primary";
        if (label == "Rien");
      };
    },
  },
  mounted() {
    this.GetEvents()
  },
  methods: {
    async GetEvents() {
      await axios
        .get("/events/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.events = res.data;
        });
    },

    async AddEvent() {
      this.event.startDate = moment(this.startDate).format("YYYY-MM-DD")
      this.event.endDate = moment(this.endDate).format("YYYY-MM-DD")
      await axios
        .post("/events/", this.event, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: 'Ajout avec succès',
            color: 'success'
          })
          this.events.push(this.event)
          this.activePromptAddEvent = false
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur d'ajout",
            text: 'veuillez vérifier vos informations',
            iconPack: 'feather',
            icon: 'icon-alert-circle',
            color: 'danger'
          })
          this.activePromptAddEvent = true
        });

    },

    async PutEvent() {
      this.eventData.startDate = moment(this.eventData.startDate).format("YYYY-MM-DD")
      this.eventData.endDate = moment(this.eventData.endDate).format("YYYY-MM-DD")
      await axios
        .put("/events/" + this.eventData.id+"/", this.eventData, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: 'Modification avec succès',
            color: 'success'
          })
          this.GetEvents()
          this.activePromptEditEvent = false
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de modification",
            text: 'veuillez vérifier vos informations',
            iconPack: 'feather',
            icon: 'icon-alert-circle',
            color: 'danger'
          })
          this.activePromptEditEvent = true
        });

    },
    DeleteEvent() {
      this.$vs.dialog({
        type: "confirm",
        color: "danger",
        title: `Confirmation de la suppression`,
        text: `Supprimer "${this.eventData.title}"`,
        accept: this.ConfirmDeleteEvent,
        acceptText: "Supprimer",
        cancelText: "Annuler",
      });
    },
    async ConfirmDeleteEvent() {
      await axios
        .delete("/events/" + this.eventData.id + "/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: 'Suppression avec succès',
            color: 'success'
          })
          this.GetEvents()
          this.activePromptEditEvent = false
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de Suppression",
            text: 'veuillez essayer plus tard',
            iconPack: 'feather',
            icon: 'icon-alert-circle',
            color: 'danger'
          })
          this.activePromptEditEvent = true
        });

    },
    eventDragged(event, date) {
      this.$store.dispatch('calendar/eventDragged', { event: event, date: date })
    },
    async GetEvent(event) {
      await axios
        .get("/events/"+event.id+"/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.eventData = res.data;
          this.labelLocal = this.eventData.label
          this.activePromptEditEvent = true;
        });
    },

    updateMonth(val) {
      this.showDate2 = this.$refs.calendar.getIncrementedPeriod(val)
      this.showDate= moment(this.showDate2).format("MM/DD/YYYY")
    },
    clearFields() {
      this.title = this.endDate = this.url = "";
      this.id = 0;
      this.labelLocal = "none";
    },
    promptAddNewEvent(date) {
      this.disabledFrom = false;
      this.addNewEventDialog(date);
    },
    addNewEventDialog(date) {
      this.clearFields();
      this.startDate = date;
      this.endDate = date;
      this.activePromptAddEvent = true;
    },
    openAddNewEvent(date) {
      this.disabledFrom = true;
      this.addNewEventDialog(date);
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/vuexy/apps/simple-calendar.scss";
</style>
